import uniLoadmore from "../../../components/uni-load-more/uni-load-more"
export default {
	data() {
		return {
			type: 0,
			nowIndex: 0,
			bar: ["全部", "待付款", "待发货", "已发货", "已关闭"],
			list: "",
			page: 1,
			more: true
		};
	},
	components: {
		uniLoadmore
	},
	computed: {},
	methods: {
		changeItem(e) {
			this.nowIndex = e;
			this.type = e;
			this.$store.dispatch("getOrder", {
				status: this.type, //1全部2待付款3待发货4已发货5已关闭
				page: 1
			}).then(res => {
				this.list = res.data;
			});
		},
		detail(id) {
			uni.navigateTo({
				url: 'order-detail?id='+id
			})
		}
	},
	onShow() {
		this.page = 1
		this.$store.dispatch("getOrder", {
			status: this.type, //1全部2待付款3待发货4已发货5已关闭
			page: this.page
		}).then(res => {
			this.list = res.data;
		}).catch(err=>{});
	},
	onPullDownRefresh() {
		this.more = true
		this.$store.dispatch("getOrder", {
			status: this.type, //1全部2待付款3待发货4已发货5已关闭
			page: 1
		}).then(res => {
			this.list = res.data;
			uni.stopPullDownRefresh()
		});
	},
	onReachBottom() {
		if (this.more) {
			this.page++
		}
		this.more = false
		this.$store.dispatch("getOrder", {
			status: this.type, //1系统2公告3激活4分润5其他
			page: this.page
		}).then(res => {
			this.more = true;
			if (this.page > 1) {
				var dataArr = this.list;
				res.data.forEach(item => {
					dataArr.push(item)
				});
				this.list = dataArr;
			} else {
				this.list = res.data;
			}
		})
	}
};
